export const Top = () => {
  const [value, setValue] = useState(0)

  return (
    <Router basename="/conan-cardgame"> {/* ベースURLを設定 */}
      <HideOnScrollAppBar>
        <Grid container>
          <Grid>
            <Tabs value={value} onChange={(event, newValue) => setValue(newValue)}>
              <Tab label="カード" component={Link} to="/cardlist" />
              {/* <Tab label="デッキ" component={Link} to="/decklist" />
              <Tab label="シミュレータ" component={Link} to="/simulator" /> */}
            </Tabs>
          </Grid>
        </Grid>
      </HideOnScrollAppBar>
      <Routes>
        <Route path="/" element={<Card setTabValue={setValue} />} />
        <Route path="/cardlist" element={<Card setTabValue={setValue} />} />
        <Route path="/decklist" element={<Deck setTabValue={setValue} />} />
        <Route path="/simulator" element={<OnePersonTool setTabValue={setValue} />} />
      </Routes>
      <Footer />
    </Router>
  );
};
