import React from 'react';


export const CardImg = ({ card , size , boxShadow }) => {

  const card2 = card 

  return (
    <img
      src={card2.number
        ? `${process.env.PUBLIC_URL}/${card2.number}.jpg`
        : 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'
      }
      alt=''
      style={{
        width: card2.type === 'case' ? size : size / 1.4,
        height: card2.type === 'case' ? size / 1.4 : size,
        borderRadius: `${size / 25}px`,
        verticalAlign: 'bottom',
        boxShadow,

      }}
    />
  );
};