import cardsByIdJson from '../data/cardsById.json';
import cardsByNumberJson from '../data/cardsByNumber.json';
import productsJson from '../data/products.json';

export const cardData = {}
export const cardList = []

const textMap = {
  パートナー:'partner',
  キャラ:'character',
  イベント:'event',
  事件:'case',
}

cardsByIdJson.forEach(jsonEl => {

  cardData[jsonEl.ID] = {
    id: jsonEl.ID,
    name: jsonEl.カード名,
    type: textMap[jsonEl.種類],
    colors: ['色1', '色2'].map(key => jsonEl[key]).filter(Boolean),
    tags: jsonEl.特徴.split('_'),
    level: jsonEl.レベル,
    actionPoint: jsonEl.AP,
    logicPoint: jsonEl.LP,
    abilities: ['能力1', '能力2', '能力3'].map(ability => jsonEl[ability]).filter(Boolean),
    cutIn: jsonEl.カットイン,
    inspiration: jsonEl.ヒラメキ,
    numbers: [],
  }
})

cardsByNumberJson.forEach(jsonEl => {

  if( jsonEl.レア === 'SEC') return;

  cardList.push({
    number: jsonEl['NO.'],
    rarity: jsonEl.レア,
    product: jsonEl.商品,
    ...cardData[jsonEl.ID],
  })

  if (jsonEl.パラレル) {
    cardList.push({
      number: jsonEl['NO.']+'P',
      rarity: jsonEl.レア+'P',
      product: jsonEl.商品,
      ...cardData[jsonEl.ID],
    })
  }

  if (cardData[jsonEl.ID]?.numbers) {
    cardData[jsonEl.ID].numbers.push(jsonEl['NO.'])
  }

})


export const productData = (() => {
  const data = {};
  productsJson.forEach(jsonEl => {
    data[jsonEl.商品] = jsonEl;
  });
  return data;
})();

